/**
 * @module 【plugin】InteractionChecker
 * @desc 交互检查器
 * （主要检查用户是否仍真正操作）
 */
export default {
  /**
   * @func cancelAsync
   * @desc 检查用户输入
   * @param {String} checkInterval -检查区间
   * @param {Function} callback - The callback that handles the response.
   * @example import { StampPrinter } from 'awp-plugin-interaction-checker'
   *
   * StampPrinter.cancelAsync(checkInterval, callback)
   */
  checkUserInputAsync(checkInterval, callback) {
    cordova.exec(result => {
      callback(result);
    }, error => {
      console.log(error);
    }, 'CheckUserOperation', 'CheckUserInput', [checkInterval]);
  }
};
